import React, { useState } from "react";
import { Box, Container, Grid, Button, TextField } from "@material-ui/core";

export default function Login({ data, handleChangeLoginState }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const handleEmailChange = e => {
    if (e.target.value) setEmail(e.target.value);
  };
  const handlePasswordChange = e => {
    if (e.target.value) setPassword(e.target.value);
  };
  const handleSignIn = () => {
    if (email === data.email && password === data.password) {
      handleChangeLoginState(true);
    } else {
      alert("Invalid Email/Password");
    }
  };
  return (
    <Box>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Box textAlign="center">
            <Container maxWidth="lg">
              <Box style={{ paddingTop: "120px" }}>
                <Box
                  style={{
                    fontSize: "52px",
                    fontWeight: "bold",

                    fontFamily: "Squada One",
                    color: "#ed1a1a"
                  }}
                  mt={2}
                >
                  <img
                    alt="instaBookLogo"
                    src="data:image/png;base64, "
                  />
                </Box>
                <Box
                  style={{
                    fontSize: "20px",
                    fontWeight: "600"
                  }}
                  mt={5}
                  mr={18}
                >
                  Sign in
                </Box>
                <form>
                  <Box mt={2}>
                    <label>
                      <TextField
                        value={email}
                        onChange={handleEmailChange}
                        id="Email"
                        label="Email Address"
                        variant="outlined"
                      />
                      <br />
                    </label>
                    <br />
                    <label>
                      <TextField
                        value={password}
                        onChange={handlePasswordChange}
                        id="Password"
                        label="Password"
                        variant="outlined"
                        type="password"
                      />
                    </label>
                  </Box>
                  <Box mt={3}>
                    <Button
                      variant="contained"
                      color="primary"
                      type="submit"
                      onClick={handleSignIn}
                    >
                      Sign in
                    </Button>
                  </Box>{" "}
                </form>
              </Box>
            </Container>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
}
